import { useState, useEffect } from "react";
import SearchBox from "./components/SearchBox";
import noteService from "./services/notes";
import {
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Container,
  CssBaseline,
  Grid,
  Typography,
  IconButton,
  Box,
  AppBar,
  Toolbar,
} from "@mui/material";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import { red, grey } from "@mui/material/colors";
import { ReactComponent as Logo } from "./assets/barbara.svg";

const theme = createTheme({
  palette: {
    primary: {
      main: red[600],
    },
    background: {
      default: grey[100],
    },
  },
});

function App() {
  const [searchProducts, setSearchProducts] = useState();
  const [newSearch, setNewSearch] = useState();
  const [inSearch, setInSearch] = useState(false);
  const [mainProducts, setMainProducts] = useState();
  const [productsInBasket, setProductsInBasket] = useState([]);

  const displayedProducts = async (set, slice, event) => {
    if (!slice) event.preventDefault();
    try {
      const response = await fetch(
        `https://serene-eyrie-74646.herokuapp.com/https://barbora.lt/paieska?q=${newSearch}`
      );
      const stringified = await response.text();
      const regex = /var products =.*/;
      const productString = stringified
        .match(regex)[0]
        .split("=")[1]
        .replace(";", "");
      const products = await JSON.parse(productString);
      if (slice) {
        set(products.slice(0, 7));
      } else {
        set(products);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (inSearch && newSearch) {
      displayedProducts(setSearchProducts, true);
    }

    // eslint-disable-next-line
  }, [newSearch]);

  const searchSuggestions = async () => {
    try {
      if (!newSearch) {
        const response = await fetch(
          "https://serene-eyrie-74646.herokuapp.com/https://barbora.lt/api/eshop/v1/product/getSearchPlaceholderInventories"
        );
        const products = await response.json();
        setSearchProducts(products.products);
      }
      setInSearch(true);
    } catch (error) {
      console.log(error);
    }
  };

  const addToBasket = async (event, product) => {
    event.preventDefault();
    const response = await noteService.create(product);
    const products = await response.json();
    setProductsInBasket(productsInBasket.concat(products));
  };

  const productGenerator = (products) => {
    if (products) {
      return products.map((e) => (
        <Grid item xs={3}>
          <Card key={e.id} style={{ height: "400px" }}>
            <CardMedia
              component="img"
              height="200"
              image={e.image}
              alt={e.title}
              sx={{ padding: "1em 1em 0 1em", objectFit: "contain" }}
            ></CardMedia>
            <CardContent>
              <Typography>{e.title}</Typography>
              <Typography>{e.price}</Typography>
              <CardActions>
                <IconButton onClick={(event) => addToBasket(event, e)}>
                  Add To Cart
                </IconButton>
              </CardActions>
            </CardContent>
          </Card>
        </Grid>
      ));
    }
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Box sx={{ bgcolor: "background.default" }}>
        <AppBar
          color="primary"
          style={{
            position: "fixed",
            left: "50%",
            transform: "translate(-50%, 0)",
            top: 0,
          }}
        >
          <Toolbar>
            <Logo style={{ width: 55, height: 55 }} />
            <SearchBox
              inSearch={inSearch}
              setInSearch={setInSearch}
              setNewSearch={setNewSearch}
              searchSuggestions={searchSuggestions}
              searchProducts={searchProducts}
              setMainProducts={setMainProducts}
              displayedProducts={displayedProducts}
              productGenerator={productGenerator}
            />
          </Toolbar>
        </AppBar>
        <Grid container>
          <Grid
            container
            item
            xs={8}
            spacing={3}
            style={{ marginTop: "100px" }}
          >
            {productGenerator(mainProducts)}
          </Grid>
          <Grid>
            <Box></Box>
          </Grid>
        </Grid>
      </Box>
    </ThemeProvider>
  );
}
export default App;
